.container {
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1280px;
    margin-inline: auto;
    padding-inline: 55px;
  }
}

* {
  border: none;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a, a:link, a:visited, a:hover {
  text-decoration: none;
}

aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: #0000;
}

input::-ms-clear {
  display: none;
}

button, input[type="submit"] {
  box-shadow: none;
  cursor: pointer;
  background-color: #0000;
  display: inline-block;
}

input:focus, input:active, button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  scroll-behavior: smooth;
  color: #0f0e08;
  background-color: #fff;
  font-family: IBM Plex Sans, serif;
}

html:has(.menu:target) {
  overflow: hidden;
}

body {
  background-color: #fff;
  min-width: 320px;
}

.logo {
  text-align: center;
  color: #0f0e08;
  text-transform: uppercase;
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
  transition: scale .15s;
  display: inline-flex;
}

.logo:hover {
  scale: 1.1;
}

.logo:active {
  scale: .95;
}

.icon {
  background-position: center;
  background-size: cover;
  width: 24px;
  height: 24px;
  transition: scale .15s;
  display: block;
}

.icon:hover {
  scale: 1.1;
}

.icon:active {
  scale: .95;
}

.icon--menu {
  background-image: url("menu.17f3ebeb.svg");
}

.icon--close {
  background-image: url("close.577668ab.svg");
}

.icon--facebook {
  background-image: url("facebook.475c5902.svg");
}

.icon--instagram {
  background-image: url("instagram.38fd1dfa.svg");
}

.menu {
  z-index: 4;
  color: #fff;
  opacity: 0;
  pointer-events: none;
  background-color: #1a5a4c;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.menu__container {
  flex-direction: column;
  max-width: 320px;
  margin-inline: auto;
  display: flex;
}

@media (width >= 640px) {
