.title-h2 {
  font-family: Montserrat, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
  text-align: center;
  margin-bottom: 40px;

  @include on-tablet {
    font-size: 48px;
    line-height: 48px;
    text-align: left;
    margin-bottom: 48px;
  }

  @include on-desktop {
    margin-bottom: 56px;
  }
}
